@import url(https://fonts.googleapis.com/css2?family=Jua&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#spin-spin {
  text-align: center;
  margin-top: 30px;
}

* {
  line-height: normal;
  font-family: "Jua", sans-serif;
}

html,
body,
#root,
#root > div {
  height: 100%;
}

#header {
  height: 84px;
  display: flex;
  justify-content: center;
  background-color: white;
}

#header-area {
  width: 1024px;
  height: 100%;
  margin: 0 auto;
  display: flex;
  align-items: flex-end;
  display: flex;
  justify-content: space-between;
}

#header-cat {
  height: 55px;
  display: flex;
  justify-content: center;
  background-color: white;
  border-top: 1px solid gainsboro;
  border-bottom: 1px solid gainsboro;
}

#header-area-cat {
  width: 1024px;
  height: 100%;
  margin: 0 auto;
  display: flex;
  align-items: flex-end;
  display: flex;
  justify-content: space-between;
}

#logo {
  width: 210px;
  height: 60px;
  margin-bottom: 10px;
}

#profile {
  width: 60px;
}
#profile2 {
  height: 45px;
  width: 45px;
  border-radius: 30px;
}

#body {
  min-height: 100vh;
  max-width: 1024px;
  width: 100%;
  margin: 0 auto;
  padding-bottom: 24px;
}

#footer {
  height: 200px;
  background-color: rgb(230, 230, 230);
}

.k-button {
  font-size: x-large;
  height: 100% !important;
  border: none;
}
.k-button2 {
  height: 100% !important;
  font-size: x-large;
  border: none;
}
.k-button3 {
  height: 100% !important;
  border: none;
}

* {
  margin: 0;
  padding: 0;
}

#spin-spin {
  text-align: center;
  margin-top: 30px;
}

@media screen and (max-width: 400px) {
  .product-card {
    width: 160px !important;
  }
  #header {
    padding: 0 8px;
  }
  #body {
    padding: 0 8px;
  }
  .ant-carousel {
    margin: 0 -8px;
  }
}

#banner {
    max-height: 300px;
}

#banner>img {
    width: 100%;
}

#body>h1 {
    margin-top: 16px;
}

#product-list {
    margin-top: 12px;
    display: flex;
    flex-wrap: wrap;
}

.product-card {
    width: 180px;
    height: 300px;
    margin-right: 12px;
    margin-bottom: 12px;
    border: 1px solid rgb(230, 230, 230);
    border-radius: 12px;
    overflow: hidden;
    position: relative;
}

.product-blur {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ffffffaa;
    pointer-events: none;
}

.product-img {
    width: 100%;
    height: 210px;
    object-fit: contain;
}

.product-contents {
    display: flex;
    flex-direction: column;
    padding: 8px;
}

.product-name {
    font-size: 14px;
}

.product-price {
    font-size: 16px;
    font-weight: 600;
    margin-top: 4px;
}

.product-seller {
    display: flex;
    align-items: center;
}

.product-avatar {
    width: 24px;
}

.product-link {
    color: inherit;
    text-decoration: none;
}

.product-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 12px; 
}

.product-date {
    font-size: 12px;
    color: rgb(136, 136, 136);
}

#product-headline {
    margin-top: 8px;
}


#upload-img-placeholder {
    width: 200px;
    height: 200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: 1px solid rgb(230, 230, 230);
    background-color: rgb(250, 250, 253);
    margin-left: 16px;
}

#upload-container {
    padding-top: 36px;
}

#upload-img-placeholder > img {
    width: 50px;
    height: 50px;
}

#upload-img-placeholder > span {
    font-size: 14px;
    color: rgb(195, 194, 204);
}

.upload-name {
    width: 200px;
}

.upload-label {
    font-size: 18px;
    width: 80px;
    text-align: left;
}

#product-description {
    height: 150px;
