#banner {
    max-height: 300px;
}

#banner>img {
    width: 100%;
}

#body>h1 {
    margin-top: 16px;
}

#product-list {
    margin-top: 12px;
    display: flex;
    flex-wrap: wrap;
}

.product-card {
    width: 180px;
    height: 300px;
    margin-right: 12px;
    margin-bottom: 12px;
    border: 1px solid rgb(230, 230, 230);
    border-radius: 12px;
    overflow: hidden;
    position: relative;
}

.product-blur {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ffffffaa;
    pointer-events: none;
}

.product-img {
    width: 100%;
    height: 210px;
    object-fit: contain;
}

.product-contents {
    display: flex;
    flex-direction: column;
    padding: 8px;
}

.product-name {
    font-size: 14px;
}

.product-price {
    font-size: 16px;
    font-weight: 600;
    margin-top: 4px;
}

.product-seller {
    display: flex;
    align-items: center;
}

.product-avatar {
    width: 24px;
}

.product-link {
    color: inherit;
    text-decoration: none;
}

.product-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 12px; 
}

.product-date {
    font-size: 12px;
    color: rgb(136, 136, 136);
}

#product-headline {
    margin-top: 8px;
}

